/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

 .wp-block-create-block-game-review {
	padding: 2px;

	.text {
		
		padding: 1em;
		margin-bottom: 1em;
		color: #fff;
		background-color: #337EB9;
		a {
			color: #fff;
			border-bottom: none;
			text-decoration: none;
		}
		.item {
			.fn {
				font-weight: 700;
			}
		}
	}

	.shortscore-hreview {
		overflow: hidden;
		padding: 0;
		margin-bottom: 1em;
		.rating {
			float: right;
			font-size: 300%;
			color: #fff;
			line-height: 1.2;
			padding: 0;
			a {
				display: block !important;
				border-bottom: none !important;
				color: #fff !important;
				text-decoration: none !important;
				box-shadow: none !important;
				-webkit-box-shadow: none !important;
				&:hover {
					color: #fff;
				}
			}
		}
		.link {
			float: left;
			padding: 1em;
			padding-top: 0;
			a {
				text-decoration: none;
			}
		}
		
		.summary {
			font-size: 110%;
		}
		.outof {
			display: none;
		}
		.dtreviewed {
			display: none;
		}
		.best {
			display: none;
		}
	}
	.shortscore-title {
		color: #108D4F;
		text-align: center;
		margin-bottom: 1em;
		a {
			color: #108D4F;
			text-decoration: none;
			border: none;
			text-decoration: none;
		}
	}
	.shortscore {
		background-repeat: no-repeat;
		background-position: -400px -600px;
		background-size: 600px;
		background-image: url('../assets/shortscore-600x800.png?v=1.0');
		background-image: url('../assets/shortscore.svg?v=1.0'), linear-gradient(transparent, transparent);
		text-align: center;
		width: 200px;
		height: 200px;
		line-height: 200px;
		font-size: 60px;
		font-weight: 700;
		margin: 0 auto;
		margin-bottom: 0.4em;
	}
	.shortscore-1 {
		background-position: -200px 0;
	}
	.shortscore-2 {
		background-position: -400px 0;
	}
	.shortscore-3 {
		background-position: 0 -200px;
	}
	.shortscore-4 {
		background-position: -200px -200px;
	}
	.shortscore-5 {
		background-position: -400px -200px;
	}
	.shortscore-6 {
		background-position: 0 -400px;
	}
	.shortscore-7 {
		background-position: -200px -400px;
	}
	.shortscore-8 {
		background-position: -400px -400px;
	}
	.shortscore-9 {
		background-position: 0 -600px;
	}
	.shortscore-10 {
		background-position: -200px -600px;
	}
}
.rangeslider {
	.rangeslider__fill {
		background-color: #337EB9;
	}
}
.shortscore {
	* {
		user-select: none;
	}
}
.score {
	text-align: center;
	margin-bottom: 2em;
	color: #707070;
}

@media only screen and (min-resolution: 192dpi), only screen and (min-device-pixel-ratio: 2)  {
	html {
		.wp-block-create-block-game-review {
			.shortscore {
				background-image: url('../assets/shortscore-1200x1600.png');
				background-image: url('../assets/shortscore.svg'), linear-gradient(transparent, transparent);
			}
		}
	}
}
